import { Component } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { IFeature } from 'src/shared/models/common-type';
import { AuthenticationConstants } from '../app.constants';

@Component({
  selector: 'app-welcome-features',
  templateUrl: './welcome-features.component.html',
  styleUrls: ['./welcome-features.component.scss'],
})
export class WelcomeFeaturesComponent {
  features: IFeature[] = AuthenticationConstants.welcome_features;
  constructor(
    private readonly spinner: NgxSpinnerService,
  ) { }

  ionViewDidEnter() {
    this.spinner.hide("appSpinner")
  }
}
